import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import "./../styles/place-graphic.scss"

export default class FrontGraphic extends React.Component {
    render() {
        return (

<StaticQuery
    query={graphql`
        query {
            allFile(filter: {relativeDirectory: {eq: "img"}}) {
                edges {
                    node {
                        relativePath
                        childImageSharp {
                            fixed(width: 700) {
                                ...GatsbyImageSharpFixed
                            }
                        }
                    }
                }
            }
        }
    `}
    render={data => {
        const place_data = [
            {
                country: 'Bolivia',
                filename: 'thumb__MG_5127_1024.jpg',
                link: '/bolivia',
            },
            {
                country: 'Botswana',
                filename: 'DSC_0746.jpg',
                link: '/botswana',
            },
            {
                country: 'Burundi',
                filename: 'DSC_0380(1).jpg',
                link: '/burundi',
            },
            {
                country: 'Cambodia',
                filename: 'DSC_0483.jpg',
                link: '/cambodia',
            },
            {
                country: 'DR Congo',
                filename: 'DSC_0711.jpg',
                link: '/dr-congo',
            },
            {
                country: 'Myanmar (Burma)',
                filename: 'Leik Ai Village (2).jpg',
                link: '/burma',
            },
            {
                country: 'United States',
                filename: '_MG_5699.jpg',
                link: '/united-states',
            },
            {
                country: 'Vietnam',
                filename: 'Le Te.jpg',
                link: '/vietnam',
            },
            {
                country: 'Zambia',
                filename: 'DSC_0362.jpg',
                link: '/zambia',
            },
        ]
        return (
            <div id="place-graphic-wrapper" className="places">
            {
                place_data.map((place, index) =>
                    <div className="place-link" key={index}>
                        <Link to={"/places" + place.link}>
                            <Img
                                fixed={data.allFile.edges.find(({node}) => node.relativePath === 'img/' + place.filename).node.childImageSharp.fixed}
                                style={{width: '100%', height: '100%'}}
                            />
                            <div className="caption">
                                <div className="caption-inner">
                                    <span className="border inner-right"></span>
                                    <span className="border inner-left"></span>
                                    <span className="border inner-bottom"></span>
                                    <span className="border inner-top"></span>
                                    {place.country}
                                </div>
                            </div>
                        </Link>
                    </div>
                )
            }
            </div>
        )
    }}
/>

           )
    }
}

